  float: left;
  width: 100%;
}
body #header {
  width: 100%;
  float: left;
}
body #prefix {
  width: 100%;
  float: left;
}
body #core {
  width: 100%;
  float: left;
}
body #postfix {
  width: 100%;
  float: left;
}
