@media print {
  body {
    background: #fff;
    -webkit-print-color-adjust: exact;
    print-color-adjust: exact;
  }

  #page-wrap {
    background: #fff;
  }

  #header {
    display: block;
  }
  #header #s-header__cpass {
    background: #002855;
    box-shadow: 0 0 0 10000px #002855 inset;
  }
  #header #s-header__cpass .b-logo {
    float: left;
    background: #002855;
    width: 100%;
    display: block;
    box-shadow: 10000px 10000px 10000px 10000px #002855 inset;
    content: 'cpass';
    color: #fff;
  }
  #header #s-header__cpass .m-mastUtils {
    display: none;
  }
  #header #s-header__site {
    background: #fff;
  }
  #header #s-header__site .site-logo {
    color: #002855;
    display: block;
  }
  #header #s-header__site #nav-primary {
    display: none;
  }

  #prefix {
    background: #fff;
  }
  #prefix .page-name {
    color: #6e6259;
  }
  #prefix .breadcrumbs {
    color: #2C2A29;
  }
  #prefix nav {
    display: none;
  }

  #core {
    background: #fff;
  }
  #core nav {
    display: none;
  }

  #postfix {
    display: none;
  }

  #footer #s-footer__site {
    background: #f7f7f7;
  }
  #footer #s-footer__site .b-contact__info {
    color: #2C2A29;
  }
  #footer #s-footer__site .b-contact__info a {
