.clearfix {
	//@if $legacy-support-for-ie6 or $legacy-support-for-ie7 {
    //*zoom: 1;
  //}

  &:before,
  &:after {
    display: table;
    content: "";
  }

  &:after {
    clear: both;
  }
}


.white-noise{
  background-image:url('../images/noise.png');
}