.b-search{
  *:focus{
    outline:0;
  }
  input:focus{
    outline:0;
  }
  
  .form-search {
   clear: both; 
   background:none;
   box-shadow:none;
   @include rhythm(0, $ms-3n, 0, 0, $font-size-med);
  }

  .form-search__label-text {
    position: absolute; top: -99999em; 
  }

  .form-search__search-type {
  @include rhythm(0, $ms-2n, 0, 0, $font-size-small-2);
    label {
      @include adjust-font-size-to($font-size-small-2);
      
      @extend .font-Roman;
      color:$white;
      margin-right:ms(-2);
      margin-left:ms(-6);
      width:auto;
      float:left;
      margin-top:-2px;
    }
  input[type="radio"] {
      background-color: $coolblack2;
      border-radius: ms(-1);
      border-color:$coolblack;
      box-shadow:0 ms(-11) 0 0 tint($coolblack, 20), inset 0 ms(-4) ms(-2) ms(-11) shade($coolblack, 10);
      cursor: pointer;
      display: inline-block;
      height: ms(0);
      position: relative;
      width: ms(0);
      -webkit-appearance: none;
      margin:0;
      float:left;
      *:focus{
        outline:0;
      }

  }
  input[type="radio"]:after {
      background-color: $black;
      border-radius: 25px;
      //box-shadow: inset 0 0 0 1px hsla(0,0%,0%,.4),                  0 1px 1px hsla(0,0%,100%,.8);
      content: '';
      display: block;
      height: ms(-3);
      left: ms(-7);
      position: relative;
      top: ms(-7);
      width: ms(-3);
      *:focus{
        outline:0;
      }
  }
  input[type="radio"]:checked:after {
      background-color: $WVUGold;
      box-shadow: inset 0 0 0 1px set-alpha($WVUGold, 0.4),
                  inset 0 2px 2px set-alpha($WVUGold, 0.4),
                  0 1px 1px set-alpha($WVUGold, 0.8),
                  0 0 2px 2px hset-alpha($WVUGold, 0.4);
       *:focus{
        outline:0;
       }           
    }//end type="radio"
  }//end wvu-search__search-type

.wvu-search__search-bar { padding-top: 2.1em; font-size: 0.8em;
  input[type="checkbox"], input[type="radio"] { margin-right: 0; }
}

.wvu-search__search-form { clear: both; }
.wvu-search__search-box { color: $black; clear: both; }

input[type="search"] {
  &.wvu-search__search-box { margin-bottom: 0.25em; }
}

.form-search__search-type { clear: both; 
  @include mq($bp-medium) { padding-top: 0; }
  input {
    &:last-child { margin-left: 1em; }
  }
}

#q {
  margin-bottom: 0;
}

input[type="submit"] {
  &.form-search__submit {

    color: $white;
    height: 30px;
    text-indent: -99999em;
    border: 2px solid white;
    width:20%;
    background-color: transparent;
    background-image: url(../images/search-icon.png);
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: 50% ms(-6);
    padding: ms(-6) ms(-6) 0 ms(-6);
   
    &::before{
    font-family: 'icomoon';
      content:"\e68f";
      @include adjust-font-size-to($font-size-large);
      @include standard-text-shadow($WVUBlue);
    }
    &:hover {
      background-color: $WVUGold;
      border-color: tint($WVUGold, 2);
    }

     img { max-height: 20px; }
  }//end .form-search__submit
}//end type="submit"

&[data-eq-state="small"]{
    li:nth-child(3), li:nth-child(4){
      display:none;
    }
    li:nth-child(2){
      border-right:0;
      padding-right:0;
    }
  }
  &[data-eq-state="medium"]{
    li:nth-child(4){
      display:none;
    }
    li:nth-child(3){
      border-right:0;
      padding-right:0;
    }
  }
  &[data-eq-state="large"]{}
}//end .b-search


.m-primaryNav__search .b-search{
  .form-search__field{
    color:$black;
    border-bottom-color:shade($white, ms(0));
  }
   .form-search__search-type{
      color:$black;
      label{ color:tint($black, 30);}
      input[type="radio"]{
        background-color:tint($coolblack,40);
        &:after{
          background-color:tint($black, 60);
        }//end :after
        &:checked:after{
          background-color:$SecLightGold;
        }
      }//end input[type="radio"]
   }//end .form-search__search-type
   input[type="submit"] {
    &.form-search__submit {
      color:$black;
      background-color:$tertiary;
    }
  } 
}